.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#root {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px 40px;
  background-color: azure;
}

.goods-field {
  display: flex;
  justify-content: space-around;
}

.goods-block {
  text-align: center;
  border: 1px solid silver;
  border-radius: 10px;
  padding: 16px 24px;
}


table {
  margin: 30px auto;
}

td {
  border: 1px solid black;
  text-align: center;
}

td img {
  width: 13px;
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}